import PropTypes from 'prop-types'
import React, { Component } from 'react'
import { Statistic } from 'semantic-ui-react'

import axios from 'axios';

export default class PoolStats extends Component {

    state = {
        poolstats: {},
    };

    componentDidMount(){
        axios.get('http://118.25.37.83/api/stats')
        .then( ({data}) => {
            this.setState(
                { poolstats: data }
            );
        })
        .catch((err)=> {
            console.log(err)
        })
    }

    renderHashrate() {
        let hashrate = parseInt(this.state.poolstats.hashrate)
        
        if (this.state.poolstats.hashrate == undefined){
            return "0"
        } else if (hashrate < 1000) {
            return parseInt(this.state.poolstats.hashrate) + "h" 
        } else if (hashrate < 1000 * 1000) {
            return parseInt(hashrate / 1000) + "kh"
        } else {
            return parseInt(hashrate/ (1000 * 1000)) + "mh"
        }
    }
    
    renderLastBlockTime() {
        if (this.state.poolstats.stats == undefined) {return ""}

        let lastBlockTime =Math.round(new Date().getTime()/1000) - parseInt(this.state.poolstats.stats.lastBlockFound)
        if (lastBlockTime == undefined){
            return ""
        } else if (lastBlockTime < 60 ){
            return parseInt(lastBlockTime) + ''
        } else if (lastBlockTime < 60*60) {
            return parseInt(lastBlockTime/60) + ''
        } else if (lastBlockTime < 60*60*24) {
            return parseInt(lastBlockTime/(60*60)) + ""
        } else {
            return parseInt(lastBlockTime/(60*60*24)) + ""
        }
    }

    renderVariance() {
        if (this.state.poolstats.stats == undefined) {return "0"}
        console.log(this.state.poolstats.stats.roundShares)
        let percent = 100 * this.state.poolstats.stats.roundShares / this.renderDiff(true)
        if (!percent) {
            return 0;
        }else{
            return percent.toFixed(2);
        }
    }

    renderDiff(noUnit) {
        if (this.state.poolstats.nodes == undefined) {
            return ""
        } else if (this.state.poolstats.nodes.length == 0){
            return ""
        } else {
            let to = 0
            this.state.poolstats.nodes.forEach(node => {
                to += parseInt(node.difficulty)
            });
            let diff = parseInt(to / this.state.poolstats.nodes.length)
            if (noUnit) {return diff}
            if (diff < 1000) {
                return parseInt(diff) 
            } else if (diff < 1000 * 1000) {
                return parseInt(diff / 1000).toFixed(2) + "k"
            } else {
                return parseFloat(diff / (1000 * 1000)).toFixed(2) + "m"
            }
            
        }
    }

    render() {
        return(
            <div>
            <Statistic.Group widths='four' size="tiny">
                <Statistic>
                    <Statistic.Value>{ this.renderHashrate() } </Statistic.Value>
                    <Statistic.Label></Statistic.Label>
                </Statistic>
                <Statistic>
                    <Statistic.Value>{ this.renderLastBlockTime() }</Statistic.Value>
                    <Statistic.Label></Statistic.Label>
                </Statistic>
                <Statistic>
                    <Statistic.Value>{ this.state.poolstats.minersTotal || 0 }</Statistic.Value>
                    <Statistic.Label></Statistic.Label>
                </Statistic>
                <Statistic>
                    <Statistic.Value>{ this.renderVariance() + "%" }</Statistic.Value>
                    <Statistic.Label></Statistic.Label>
                </Statistic>
            </Statistic.Group>
          </div>
        );
    }
}



