import React, { Component } from 'react'
import { Statistic } from 'semantic-ui-react'

import axios from 'axios';

export default class NetworkStats extends Component {
    // constructor(props) {
    //     super(props);

    //     // this.renderRecipes = this.renderRecipes.bind(this);
    // }

    state = {
        networkstats: {}
    };

    componentDidMount(){
        axios.get('http://118.25.37.83/api/stats')
        .then( ({data}) => {
            this.setState(
                { networkstats: data }
            );
        })
        .catch((err)=> {
            console.log(err)
        })
    }

    renderHashrate() {
        let BlockTime = 12
        let hashrate = parseInt(this.renderDiff(true) / BlockTime)
        
        if (hashrate < 1000) {
            return parseInt(hashrate) + "h" 
        } else if (hashrate < 1000 * 1000) {
            return parseFloat(hashrate / 1000).toFixed(2) + "kh"
        } else {
            return parseFloat(hashrate/ (1000 * 1000)).toFixed(2) + "mh"
        }
    }

    renderDiff(noUnit) {
        if (this.state.networkstats.nodes == undefined) {
            return ""
        } else if (this.state.networkstats.nodes.length == 0){
            return ""
        } else {
            let to = 0
            this.state.networkstats.nodes.forEach(node => {
                to += parseInt(node.difficulty)
            });
            let diff = parseInt(to / this.state.networkstats.nodes.length)
            if (noUnit) {return diff}
            if (diff < 1000) {
                return parseInt(diff) 
            } else if (diff < 1000 * 1000) {
                return parseInt(diff / 1000).toFixed(2) + "k"
            } else {
                return parseFloat(diff / (1000 * 1000)).toFixed(2) + "m"
            }
            
        }
    }

    renderBlockHeight() {
        if (this.state.networkstats.nodes == undefined) {
            return "0"
        } else if (this.state.networkstats.nodes.length == 0){
            return "0"
        } else {
            let to = 0
            this.state.networkstats.nodes.forEach(node => {
                to += parseInt(node.height)
            });
            return parseInt(to / this.state.networkstats.nodes.length) 
        }
    }

    render() {
        return(
            <div>
            <Statistic.Group widths='three' size="tiny">
                <Statistic>
                    <Statistic.Value>{ this.renderHashrate() } </Statistic.Value>
                    <Statistic.Label></Statistic.Label>
                </Statistic>
                <Statistic>
                    <Statistic.Value>{ this.renderDiff() }</Statistic.Value>
                    <Statistic.Label></Statistic.Label>
                </Statistic>
                <Statistic>
                    <Statistic.Value>{  this.renderBlockHeight() }</Statistic.Value>
                    <Statistic.Label></Statistic.Label>
                </Statistic>
            </Statistic.Group>
          </div>
        );
    }
}



