import React, { Component } from 'react'
import { Segment, Input, Header, Icon, Button, Tab, Dimmer, Loader, Statistic, Table } from 'semantic-ui-react'

import axios from 'axios';

export default class UserStats extends Component {

    state = {
        account: "",
        err: "",
        userstats: undefined,
        profile_paid: 0,
        profile_balance: 0,
        profile_pending: 0,
        payments: [],
        workers: {}
    };

    componentDidMount(){
        // fetch account from cookie
    }
    //     axios.get('http://118.25.37.83/api/stats')
    //     .then( ({data}) => {
    //         this.setState(
    //             { poolstats: data }
    //         );
    //     })
    //     .catch((err)=> {
    //         console.log(err)
    //     })
    // }

    renderHashrate(hashrate) {
        // if (!this.state.userstats){return 0}
        // let hashrate = 0
        // if (isCurrent){
        //     if (this.state.userstats.currentHashrate) {let hashrate = parseInt(this.state.userstats.currentHashrate)}
        // }else{
        //     if (this.state.userstats.hashrate) {let hashrate = parseInt(this.state.userstats.hashrate)}
        // }
        
        if (hashrate == undefined){
            return "0"
        } else if (hashrate < 1000) {
            return parseInt(hashrate) + "h" 
        } else if (hashrate < 1000 * 1000) {
            return parseInt(hashrate / 1000) + "kh"
        } else {
            return parseInt(hashrate/ (1000 * 1000)) + "mh"
        }
    }

    renderLastSubmitTime() {
        if (!this.state.userstats) {return ""}
        if (!this.state.userstats.stats) {return ""}

        let lastBlockTime =Math.round(new Date().getTime()/1000) - parseInt(this.state.userstats.stats.lastShare)
        if (lastBlockTime == undefined){
            return ""
        } else if (lastBlockTime < 60 ){
            return parseInt(lastBlockTime) + ''
        } else if (lastBlockTime < 60*60) {
            return parseInt(lastBlockTime/60) + ''
        } else if (lastBlockTime < 60*60*24) {
            return parseInt(lastBlockTime/(60*60)) + ""
        } else {
            return parseInt(lastBlockTime/(60*60*24)) + ""
        }
    }

    formatCoinUnit(amount) {
        return (amount * 0.000000001).toFixed(2) 
    }

    renderUserStatsDOM = (e) => {
        if (e.target.value.length != 42) {
            this.setState({ready: false})

            this.setState({err: ""})

        } else {
            this.setState({ account: e.target.value})
            axios.get("http://118.25.37.83/api/accounts/" + e.target.value)
            .then(({data}) => {
                this.setState({userstats: data})

                this.setState({profile_pending: this.formatCoinUnit(data.stats.pending)})
                this.setState({profile_paid: this.formatCoinUnit(data.stats.paid)})
                this.setState({profile_balance: this.formatCoinUnit(data.stats.balance)})

                this.setState({payments: data.payments})
                this.setState({workers: data.workers})
                console.log(data.workers)

                this.setState({ready: true})
            })
            .catch((err)=>{
                this.setState({err: err})
            })
        }
    }

    render() {

        let panes = [
            { menuItem: '', render: () => 
            <Tab.Pane attached={false}>
                <Statistic.Group widths='three' size="tiny">
                    <Statistic>
                        <Statistic.Value>{ this.renderHashrate(this.state.userstats.currentHashrate) } </Statistic.Value>
                        <Statistic.Label></Statistic.Label>
                    </Statistic>
                    <Statistic>
                        <Statistic.Value>{ this.renderHashrate(this.state.userstats.hashrate) } </Statistic.Value>
                        <Statistic.Label></Statistic.Label>
                    </Statistic>
                    <Statistic>
                        <Statistic.Value>{ this.renderLastSubmitTime() }</Statistic.Value>
                        <Statistic.Label></Statistic.Label>
                    </Statistic>
                    <Statistic>
                        <Statistic.Value>{ this.state.profile_paid }</Statistic.Value>
                        <Statistic.Label></Statistic.Label>
                    </Statistic>
                    <Statistic>
                        <Statistic.Value>{ this.state.profile_balance }</Statistic.Value>
                        <Statistic.Label></Statistic.Label>
                    </Statistic>
                    <Statistic>
                        <Statistic.Value>{ this.state.profile_pending }</Statistic.Value>
                        <Statistic.Label></Statistic.Label>
                    </Statistic>
                </Statistic.Group>
            </Tab.Pane> },
            { menuItem: '', render: () => 
            <Tab.Pane attached={false}>
                <Table celled>
                    <Table.Header>
                        <Table.HeaderCell></Table.HeaderCell>
                        <Table.HeaderCell></Table.HeaderCell>
                        <Table.HeaderCell></Table.HeaderCell>
                        <Table.HeaderCell></Table.HeaderCell>
                    </Table.Header>

                    <Table.Body>
                    { this.state.workers && Object.keys(this.state.workers).map((key)=>
                        <Table.Row key={key}>
                            <Table.Cell>{ key }</Table.Cell>
                            <Table.Cell>{new Date(this.state.workers[key].lastBeat*1000).toLocaleString() }</Table.Cell>
                            <Table.Cell>{this.state.workers[key].offline=="true"?"":""}</Table.Cell>
                            <Table.Cell>{this.renderHashrate(this.state.workers[key].hr2)}</Table.Cell>
                        </Table.Row>
                        ) }
                    </Table.Body>
                </Table>
            </Tab.Pane> },
            { menuItem: '', render: () => 
            <Tab.Pane attached={false}>
                <Table celled>
                    <Table.Header>
                        <Table.HeaderCell></Table.HeaderCell>
                        <Table.HeaderCell></Table.HeaderCell>
                        <Table.HeaderCell>ID</Table.HeaderCell>
                    </Table.Header>

                    <Table.Body>
                    { this.state.payments && this.state.payments.map((obj)=>
                        <Table.Row key={obj.tx}>
                            <Table.Cell>{this.formatCoinUnit(obj.amount)}</Table.Cell>
                            <Table.Cell>{new Date(obj.timestamp*1000).toLocaleString() }</Table.Cell>
                            <Table.Cell>{obj.tx}</Table.Cell>
                        </Table.Row>
                        ) }
                    </Table.Body>
                </Table>
            </Tab.Pane> },
        ]

        return(
            <div>
            <Input fluid label='(0x)' placeholder='' onChange={ this.renderUserStatsDOM } />
            <Segment placeholder={this.state.account == "" || this.state.account.length != 42 || this.state.err!=undefined}>
                {
                    (this.state.ready)? 
                    <Tab menu={{ secondary: true, pointing: true }} className="hidden" panes={panes} />:
                    <Header icon><Icon name='warning sign' />{this.state.err}</Header>
                }
            </Segment>
            </div>
        );
    }
}





