import React from 'react'
import { Icon, Header, Segment, Input, Container, Tab, Button, Grid, GridColumn, Form, TextArea, Sidebar, Menu } from 'semantic-ui-react'
import { Navbar, PoolStats, NetworkStats, UserStats } from 'components'

import 'styling/semantic.less'

var state = {}

const leftItems = [
  // {
  //   as: 'a',
  //   content: 'Documentation',
  //   href: 'https://react.semantic-ui.com/',
  //   icon: 'book',
  //   key: 'docs',
  //   target: '_blank',
  // },
]

const rightItems = [
  {
    as: 'a',
    content: 'WebchainQQ',
    href: 'https://jq.qq.com/?_wv=1027&k=5tyLBix',
    icon: 'qq',
    key: 'qq',
    target: '_blank',
  },
  // {
  //   as: 'a',
  //   content: 'Stack Overflow',
  //   icon: 'stack overflow',
  //   href: 'https://stackoverflow.com/questions/tagged/semantic-ui-react?sort=votes',
  //   key: 'so',
  //   target: '_blank',
  // },
]

const panes = [
  { menuItem: 'Windows', render: () => 
    <Tab.Pane>
      Windows<a href="https://github.com/webchain-network/webchain-miner/releases/download/v2.6.2.0/webchain-miner-2.6.2.0-win64.zip"></a>config.json<b> node.webchain.webchain.top:3333</b>Start
    </Tab.Pane> 
  },
  { menuItem: 'Linux', render: () => 
    <Tab.Pane>
      Linux<a href="https://github.com/webchain-network/webchain-miner/releases/download/v2.6.2.0/webchain-miner-2.6.2.0-linux-amd64.tar.gz"></a>config.json<b> node.webchain.webchain.top:3333</b>
    </Tab.Pane> 
  },
  { menuItem: 'MacOS', render: () => 
    <Tab.Pane>
      MacOSLinux
    </Tab.Pane> 
  },
  { menuItem: 'ARM(Android)', render: () => 
  <Tab.Pane>
    ARM<a href="https://github.com/webchain-network/webchain-miner/releases/download/v2.6.2.0/webchain-miner-android.apk">APK</a><b> node.webchain.webchain.top:3333</b>
  </Tab.Pane> 
},
]

const App = () => (
  <Navbar leftItems={leftItems} rightItems={rightItems}>

    <Grid>

    <Grid.Column mobile={16} computer={16}>
    <Segment>
        <Grid>
        <Grid.Column mobile={16} computer={8}>
        <Segment>
            <Header as='h2'></Header>
            <Container>
            <NetworkStats></NetworkStats>
            </Container>
        </Segment>
        </Grid.Column>

        <Grid.Column mobile={16} computer={8}>
        <Segment>
            <Header as='h2'></Header>
            <Container>
            <PoolStats></PoolStats>
            </Container>
        </Segment>
        </Grid.Column>

        <Grid.Column mobile={16} computer={16}>
        <Segment>
            <Header as='h2'></Header>
            <Container>
            <Tab panes={panes} />
            </Container>
        </Segment>
        </Grid.Column>
        
        </Grid>
    </Segment>
    </Grid.Column>

    <Grid.Column mobile={16} computer={16}>
    <Segment>
        <Header as='h2'></Header>
        <UserStats></UserStats>
    </Segment>
    </Grid.Column>

    </Grid>

  </Navbar>
)

export default App



